import { gql } from 'graphql-tag';

const typeDefs = gql`
    Query user {
        user: VerifiedUser
    }

  type VerifiedUser @key(fields: _id){
    _id: ID!
    username: String!
    email: String!
    password: String!
    type: Type!
    verificationCode: String!
    passwordResetCode: String
    verified: boolean
  }

  type UnVerifiedUser @key(fields: _id){
    _id: ID!
    username: String!
    email: String!
    password: String!
    type: Type!
    passwordResetCode: String
    verified: boolean
  }

  enum Type{
    OWNER 
    AGENT
    RENTER
  }

  input createUnVerifiedUserInput {
    username: String!
    email: String!
    password: String!
    type: Type!
    passwordResetCode: String
    verified: boolean
  }

  Mutation {
    createUser(input: CreateUnverifiedUserInput!):UnverifiedUser
  }
`;

export default typeDefs
